// 导入 axios 实例
import service from "..";

// 获取动态消息(只能获取20条)
export function getEvent(cookie) {
  return service({
    method: 'GET',
    url: `/event?cookie=${cookie}`
  })
}

// 获取热门话题
export function getHotTopic(cookie) {
  return service({
    method: 'GET',
    url: `/hot/topic?cookie=${cookie}`
  })
}

// 获取用户关注列表(需登录)
export function getUserFollows(uid, limit, cookie) {
  return service({
    method: 'GET',
    url: `/user/follows?uid=${uid}&limit=${limit}&cookie=${cookie}`
  })
}

// 获取用户粉丝列表(需登录)
export function getUserFolloweds(uid, limit, cookie) {
  return service({
    method: 'GET',
    url: `/user/followeds?uid=${uid}&limit=${limit}&cookie=${cookie}`
  })
}

// 获取用户动态(需登录)
export function getUserEvents(uid, limit, cookie) {
  return service({
    method: 'GET',
    url: `/user/event?uid=${uid}&limit=${limit}&cookie=${cookie}`
  })
}