import { getEvent, getHotTopic } from '@/request/api/focus';
import { getUserDetail } from '@/request/api/personalHome';
import { mapMutations } from 'vuex';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 动态
      events: [],
      // 用户信息
      userInfo: [],
      // 用户详情
      profile: [],
      // 热门话题
      hotTopics: [],
      // 滚动时是否固定热门话题在后侧
      isHotTopicShow: 0 // html渲染的标签
      // vHtmlTag: '',
      // 是否显示 v-html 嵌入的 html标签
      // isVHtmlTag: 0

    };
  },

  async mounted() {
    if (sessionStorage.getItem("cookie")) {
      // 获取热门话题
      const res = await getHotTopic(sessionStorage.getItem("cookie")); // console.log('获取热门话题', res);

      this.hotTopics = res.data.hot; // console.log('热门话题', this.hotTopics);
      // 获取动态消息(动态数量更准确)

      const res1 = await getEvent(sessionStorage.getItem("cookie")); // console.log('获取动态消息', res1);

      this.events = res1.data.event; // console.log('动态消息', this.events);

      if (this.events.length !== 0) this.isLoading = 0;
    }

    if (sessionStorage.getItem('userInfo')) {
      this.userInfo = JSON.parse(sessionStorage.getItem('userInfo')); // console.log(this.userInfo);
      // 直接获取用户详情

      const res = await getUserDetail(this.userInfo.userid, sessionStorage.getItem("cookie"));
      this.profile = res.data.profile; // console.log('用户详情', this.profile);
    }
  },

  created() {
    // 添加监听器，并给监听器绑定滚动方法 handleScroll()
    // 节流(连续触发事件但是在 n 秒中只执行一次函数。节流会稀释函数的执行频率)
    document.addEventListener('scroll', this.throttle(this.handleScroll, 1000), true);
  },

  beforeDestroy() {
    // 去除监听器
    document.removeEventListener("scroll", this.listenerFunction);
  },

  methods: {
    // 去往个人主页
    gotoPersonalHome(userId) {
      this.$router.push({
        path: '/PersonalHome',
        query: {
          userId: userId
        }
      });
    },

    // 点击动态
    eventClick(item) {
      // 播放音乐
      if (item.type == 18) {
        // console.log(item);
        // 先从 json 字符串中获取歌曲信息
        // console.log(this.formatJson(item.json).song);
        // 临时播放列表(只有点击动态的这一首歌)
        let tempSongs = [];
        tempSongs.push(this.formatJson(item.json).song); // console.log('临时播放列表 tempSongs', tempSongs);
        // 显示底部音乐播放组件

        this.updateIsPlayBox(1); // 更新播放列表

        this.updatePlayList(tempSongs); // 更新歌单名(歌曲来源)

        this.updateSongListName('动态'); // 更新歌单 id(歌曲来源)

        this.updateSongListId(0); // 更新播放列表下标

        this.updatePlayListIndex(0);
      } // 去往歌单详情页


      if (item.type == 13) {
        // console.log(item);
        // console.log(this.formatJson(item.json).playlist);
        this.$router.push({
          path: '/SongListDetail',
          query: {
            // 先从 json 字符串中获取歌单信息
            id: this.formatJson(item.json).playlist.id,
            // 1歌单 2专辑
            type: 1
          }
        });
      }
    },

    // 时间戳转日期
    formatPublishTime(time) {
      const date = new Date(time);
      const M = date.getMonth() + 1;
      const D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      const H = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
      const Min = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes(); // 获取当前时间

      const MNow = new Date().getMonth();
      const DNow = new Date().getDate(); // console.log(MNow + 1, DNow);
      // 判断是否当天发的动态

      return M == MNow + 1 && D == DNow ? H + ':' + Min : M + '月' + D + '日 ' + H + ':' + Min;
    },

    // 把 json对象转换成 数组
    formatJson(json) {
      // JSON.parse() 把 json字符串 转化成 json 对象
      const data = JSON.parse(json); // console.log(data);

      return data;
    },

    // 对 description 的分段处理
    formatDescription(description) {
      let arr; // 以换行符分割每一段，每个 item 就是一段

      arr = description.split(/[(\r\n)\r\n]+/).map(item => item);
      return arr;
    },

    // 标签蓝色显示(未完成)
    tagBlue(string) {
      // 找到有标签的 item(string)，indexOf() lastIndexOf() 返回字符串中指定文本首次以及最后一次出现的位置，没有则返回 -1
      if (string.indexOf('#') !== -1 && string.lastIndexOf('#') !== -1 && string.indexOf('#') !== string.lastIndexOf('#')) {
        // console.log(string);
        // console.log(string.indexOf('#'), string.lastIndexOf('#'));
        const tagString = string.slice(string.indexOf('#'), string.lastIndexOf('#') + 1); // console.log('标签', tagString);

        const words = string.slice(string.lastIndexOf('#') + 2, string.length); // if (words !== '') console.log('标签后面的内容', words);

        return string; // 全字匹配-以字符 # 开头结尾
        // let reg = new RegExp('^#.*#$', 'g');
        // let replaceString = '<span style="color: rgb(27, 94, 195)">' + tagString + '</span>';
        // string = string.replace(reg, replaceString);
        // this.isVHtmlTag = 1
        // this.vHtmlTag = string
        // console.log(string);
        // return ''
      } else {
        // this.isVHtmlTag = 0
        return string;
      }
    },

    // 滚动方法
    handleScroll() {
      if (this.$refs.container.scrollTop) this.isHotTopicShow = this.$refs.container.scrollTop >= 760 ? 1 : 0;
    },

    // 自定义节流函数，传入需要节流的函数和节流时间
    throttle(fn, delay) {
      // 设置一个触发开关
      let canUse = true;
      return function () {
        //如果为 true，就触发技能，否则就不能触发
        if (canUse) {
          fn.apply(this, arguments); //触发技能后，关闭开关

          canUse = false; //在规定时间后打开开关

          setTimeout(() => canUse = true, delay);
        }
      };
    },

    // 去往用户关注/粉丝页
    goFollows(id, type, nickname, limit) {
      this.$router.push({
        path: '/Follows',
        query: {
          id: id,
          // 1关注 2粉丝
          type: type,
          nickname: nickname,
          limit: limit
        }
      });
    },

    // 去往用户动态页
    goTrends(id, nickname, limit) {
      this.$router.push({
        path: '/Trends',
        query: {
          id: id,
          nickname: nickname,
          limit: limit
        }
      });
    },

    ...mapMutations(['updateIsPlayBox', 'updatePlayList', 'updateSongListName', 'updateSongListId', 'updatePlayListIndex', 'updateDuration'])
  },
  watch: {
    isHotTopicShow() {
      // 固定热门话题在后侧
      if (this.isHotTopicShow) this.$refs.hotTopic.classList.add('hotTopicShow'); // 恢复原位
      else this.$refs.hotTopic.classList.remove('hotTopicShow');
    }

  }
};